.auth-form {
    width: 400px;
    padding: 20px;
    border-radius: 20px;
    background: rgba( 255, 255, 255, 0.15 );
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    backdrop-filter: blur( 15px );
    -webkit-backdrop-filter: blur( 15px );
    border: 1px solid rgba( 255, 255, 255, 0.18 );
}

.form {
    &__wrapper {
        height: 100vh;
        width: 100vw;
        display: flex;
        align-items: center;
        justify-content: center;
        background: rgb(41,46,73);
        background: linear-gradient(180deg, rgba(41,46,73,1) 50%, rgba(83,105,118,1) 100%);
    }

    &__title {
        margin-top: 0;
        font-size: 30px;
        color: $white;
        margin-bottom: 15px;
        opacity: .9;
    }

    &-group {
        position: relative;
        margin-bottom: 15px;
    }

    &-control {
        border-top: 0;
        border-left: 0;
        border-right: 0;
        border-bottom: 1px solid rgba(255, 255, 255, 0.5);
        background: none;
        outline: none;

        width: 100%;
        padding: 10px;
        padding-left: 20px;
        color: $white;
        font-size: 14px;

        &::placeholder {
            font-size: 0;
        }

        &:not(:placeholder-shown) + .form-label {
            left: 0;
            top: -8px;
            font-size: 11px;
        }
    }

    &-label {
        font-size: 14px;
        color: $white;
        position: absolute;
        left: 20px;
        top: 10px;
        opacity: .8;

        transition: all 150ms linear;

        span {
            color: red;
        }
    }
}